import { mapState, mapGetters } from 'vuex'
import Breadbar from "~/components/Breadbar.vue";
export default {
  components: [ Breadbar ],
  computed: {
    ...mapState(["accessToken"]),
  },
  watch: {
    page () {
      this.getUsers(this.page,'normal',200)
    }
  },
  data() {
    return {
      search: '',
      selectdate: false,
      dates: '',
      searchTypeSelect: '',
      pageSize: 200,
      page: 1,
      itemsPerPage: 30,
      totalRecord: 0,
      totalPage: 0,
      loading: true,
      search: '',
      searchType: [
        { text: 'uid', value: 'uid' },
        { text: 'chatbee ID', value: 'username' },
        { text: 'displayName', value: 'displayName' },
      ],
      headers: [
        { text: 'ID', align: 'start', value: 'id' },
        { text: 'Reporter', value: 'reporterUsername' },
        { text: 'Reported', value: 'reportedUsername' },
        { text: 'Date', value: 'updatedAt' },
        { text: 'Reason', value: 'reason' },
        { text: 'status', value: 'status' },
        { text: 'Action', value: 'function' },
      ],
      users: []
    }
  },
  mounted () {
    this.getUsers(1,'normal',200)
  },
  methods: {
    searchData () {
      if (this.search != '' && this.searchTypeSelect != '') {
        this.getUsers(1,'search',200)
      }
    },
    totalPageArr (totalPage) {
      let data = []
      for (var i = 1; i <= totalPage; i++) {
        data.push(i)
      }
      return data
    },
    dateget (text) {
      if (text) {
        var newtext = text.split("T")
        var newtext = newtext[1].split(".")
        return newtext[0]
      } else {
        return '-'
      }
    },
    timeget (text) {
      if (text) {
        var newtext = text.split("T")
        return newtext[0]
      } else {
        return '-'
      }
    },
    ectext (text) {
      if (text) {
        if (text.length > 8) {
            return text.slice(0,8)+'...'
        } else {
            return text
        }
      } else {
        return "-"
      }
    },
    getUsers(pageSelect,status,pageSize) {
      this.users = []
      this.loading = true
      let apiurl
      if (status == 'normal') {
        apiurl = '/v1/report-users.all?page='+pageSelect+'&pageSize=200'
      } else {
        apiurl = '/v1/report-users.all?page='+pageSelect+'&pageSize='+pageSize+'&'+this.searchTypeSelect+'='+this.search
      }
      this.$axios.$get(apiurl, {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        console.log(response.data)
        this.loading = false
        if (response.data && response.data != null) {
          this.users = response.data
          if (status == 'normal') {
            this.totalRecord = response['_metadata'].totalRecords
            this.totalPage = response['_metadata'].totalPages
          } else {
            this.totalRecord = this.users.length
            this.totalPage = Math.ceil(this.users.length / 200)
          }
        } else {
          this.users = []
          this.totalRecord = 0
          this.totalPage = 1
        }
      }).catch((error) => {
        this.loading = false
      })
    },
    viewUser () {
      this.$router.push({
        path: '/users/3X11241233'
      });
    },
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
